.frost-expand {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  font-family: Roboto, Arial, sans-serif
}

.frost-expand.collapsed .frost-icon-frost-chevron {
  -webkit-transform: rotate(-180deg);
          transform: rotate(-180deg);
}

.frost-expand.expanded .frost-icon-frost-chevron {
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
}

.frost-expand .frost-expand-label {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  margin-bottom: 5px;
  cursor: pointer;
}

.frost-expand .frost-expand-label .frost-expand-label-text {
  padding-left: 10px;
  color: #009eef;
}

.frost-expand .frost-expand-label .frost-icon-frost-chevron {
  width: 30px;
  height: 30px;
  -webkit-transition: -webkit-transform .3s;
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
}

.frost-expand .frost-expand-scroll {
  background-color: #f0f2f4;
}

.frost-expand .frost-expand-scroll .frost-expand-content {
  padding: 15px;
  color: #666;
  font-size: 14px;
  line-height: 1.4em;
}



