.frost-ajax-error-page {
  display: inline-block;
  font-family: Roboto, Arial, sans-serif;
}

.frost-ajax-error-page-description {
  margin-bottom: 10px;
  color: #404041;
  font-size: 36px;
}

.frost-ajax-error-page-details {
  white-space: pre-line;
}

.frost-ajax-error-page-expand {
  width: 600px;
  max-height: 400px;
}

.frost-ajax-error-page-http-error {
  margin-bottom: 5px;
  color: #929497;
  font-size: 18px;
}

.frost-ajax-error-page-message {
  margin-bottom: 20px;
  color: #babcbe;
  font-size: 14px;
}

.frost-ajax-error-page-suggestion {
  margin-bottom: 15px;
  color: #404041;
  font-size: 23px;
}



