@-webkit-keyframes frostFadeIn {
  from {
    opacity: 0;
    visibility: hidden;
  }

  to {
    opacity: 1;
    visibility: visible;
  }
}
@keyframes frostFadeIn {
  from {
    opacity: 0;
    visibility: hidden;
  }

  to {
    opacity: 1;
    visibility: visible;
  }
}
.frost-fade-in {
  -webkit-animation: frostFadeIn 0.2s;
          animation: frostFadeIn 0.2s;
}
@-webkit-keyframes frostFadeOut {
  from {
    opacity: 1;
    visibility: visible;
  }

  to {
    opacity: 0;
    visibility: hidden;
  }
}
@keyframes frostFadeOut {
  from {
    opacity: 1;
    visibility: visible;
  }

  to {
    opacity: 0;
    visibility: hidden;
  }
}
.frost-fade-out {
  -webkit-animation: frostFadeOut 0.2s;
          animation: frostFadeOut 0.2s;
