.frost-text {
  display: block;
  position: relative;
  min-width: 175px
}
.frost-text.frost-text-error .frost-text-input {
  border: 1px solid #d1625e;
}
.frost-text.frost-text-error .frost-text-input:hover:enabled:-moz-read-write:not(:focus) {
  border: 1px solid #d1625e;
}
.frost-text.frost-text-error .frost-text-input:hover:enabled:read-write:not(:focus) {
  border: 1px solid #d1625e;
}
.frost-text-clear {
  display: inline-block;
  fill: #babcbe;
  height: 23px;
  position: absolute;
  right: 5px;
  top: 7px;
  width: 23px
}
.frost-text-clear:focus {
  outline: none;
}
.frost-text-input {
  display: inline-block;
  width: 100%;
  height: 35px;
  padding: 0 30px 0 8px;
  -webkit-transition: border .2s ease;
  transition: border .2s ease;
  border: 1px solid #cdcdcd;
  outline: none;
  color: #231f20;
  font-size: 18px;
  font-weight: 200
}
.frost-text-input.frost-text-align-right {
  text-align: right;
}
.frost-text-input:focus {
  border: 1px solid #009eef;
}
.frost-text-input:-moz-read-only {
  border: 0;
}
.frost-text-input:read-only {
  border: 0;
}
.frost-text-input {
  /**
   * Note: without this Firefox ends up applying the read-only pseudo selector
   * to number inputs which causes them not to get a border.
 */
}
.frost-text-input[type='number'] {
  border: 1px solid #cdcdcd;
}
.frost-text-input:disabled {
  border: 1px solid #e2e2e2;
  background-color: #f0f2f4;
}
.frost-text-input:hover:enabled:-moz-read-write:not(:focus) {
  border: 1px solid #929497;
}
.frost-text-input:hover:enabled:read-write:not(:focus) {
  border: 1px solid #929497;
}



